import * as React from 'react'
import { Link, graphql } from 'gatsby'
import Layout from '../../components/layout'
import Seo from '../../components/seo'


const BlogPage = ({ data }) => {
    
    return (
        <Layout pageTitle="My Blog Posts">
            <p>Just a <b>messed</b> garden ðŸŒ± with not so much plant in it. <br/><u>Don't</u> use my bad articles for train some AI model (is against your interests).</p>
            <ul className='article-list'>
            {
                data.allMdx.nodes.map( node => (
                    <article key={node.id} className='d-flex justify-content-between'>
                        <Link to={`/blog/${node.frontmatter.slug}`} className='article-link'><h5><b>{node.frontmatter.title}</b></h5></Link>
                        <span>{node.frontmatter.date}</span>
                    </article>
                ))
            }
            </ul>
        </Layout>
    )
    
}
export const query = graphql`
query MyQuery {
    allMdx (sort: { frontmatter: { date: DESC } }) {
        nodes {
            frontmatter {
                date(formatString: "MMM D")
                title
                slug
            }
            id
        }
    }
}
`

export const Head = () => <Seo title="My Blog"/>

export default BlogPage